<template>
	<view class="shop">
		<view class="shop-news">
			<span class="font24">店铺信息</span>
			<view class="news-box">
				<view class="box1 view-diy">
					<!-- 店铺名称 -->
					<view class="flex-center">
						<view class="relive-title">
							<span class="font28">店铺名称</span>
						</view>
						<view class="relive-icon">
							<input placeholder-style="color:#CCCCCC" v-model="form.shop_name" placeholder="请填写店铺名称" />
							<image class="icon32" src="/static/image/icon_update.png" mode=""></image>
						</view>
					</view>
					<view class="fengexian"></view>
					<!-- 商家类型 -->
					<view class="flex-center">
						<view class="relive-title">
							<span class="font28">商家类型</span>
						</view>
						<view class="relive-icon">
							<picker mode="selector" @change="shopTypeChange" :value="typeIndex" :range="shopType"
								range-key="name">
								<span class="fontColor-666">{{shopType[typeIndex].name}}</span>
							</picker>
							<image class="icon22" src="/static/image/icon_gd.png" mode=""></image>
						</view>
					</view>
					<view class="fengexian"></view>
					<!-- 店铺头像 -->
					<view class="flex-center">
						<view class="relive-title">
							<span class="font28">店铺头像</span>
						</view>
						<view class="relive-icon" @click="updateImg">
							<image style='background:#CCCCCC;' class="icon60"
								:src="form.images?form.images:'/static/img/icon_xiangji.png'" mode=""></image>
							<image class="icon22" src="/static/image/icon_gd.png" mode=""></image>
						</view>
					</view>
				</view>
				<view class="box2 view-diy">
					<!-- 营业状态 -->
					<view class="flex-center">
						<view class="relive-title">
							<span class="font28">营业状态</span>
						</view>
						<view class="relive-icon">
							<picker mode="selector" @change="shopStatusChange" :value="statusIndex" :range="shopStatus"
								range-key="label">
								<view class="status">
									<view class="status-switch">
										<span class="switch" :class="form.business_status==0?'shut':'open'"></span>
										<span class="text">{{shopStatus[statusIndex].label}}</span>
									</view>
								</view>
							</picker>
							<image class="icon22" src="/static/image/icon_gd.png" mode=""></image>
						</view>
					</view>
					<view class="fengexian"></view>
					<!-- 商家地址 -->
					<view class="flex-center">
						<view class="relive-title">
							<span class="font28">商家地址</span>
						</view>
						<view class="relive-icon">
							<input placeholder-style="color:#CCCCCC" v-model="form.business_license"
								placeholder="请填写商家地址" />
							<image class="icon32" src="/static/image/icon_update.png" mode=""></image>
						</view>
					</view>
					<view class="fengexian"></view>
					<!-- 营业时间 -->
					<view class="flex-center">
						<view class="relive-title">
							<span class="font28">营业时间</span>
						</view>
						<view class="relive-icon" @click="hoursShow=true">
							<span
								class="font26 fontColor-666">{{form.service_begin_time}}-{{form.service_end_time}}</span>
							<image class="icon32" src="/static/image/icon_update.png" mode=""></image>
						</view>
					</view>
					<view class="fengexian"></view>
					<!-- 起送费 -->
					<view class="flex-center">
						<view class="relive-title">
							<span class="font28">起送费</span>
						</view>
						<view class="relive-icon">
							<uni-number-box v-model="form.initial_delivery_fee"></uni-number-box>
						</view>
					</view>
				</view>
				<view class="box3 view-diy">
					<!-- 运营点 -->
					<view class="flex-center">
						<view class="relive-title">
							<span class="font28">运营点</span>
						</view>
						<view class="relive-icon">
							<input placeholder-style="color:#CCCCCC" v-model="form.operation_point_id"
								placeholder="请填写运营点" />
							<image class="icon32" src="/static/image/icon_update.png" mode=""></image>
						</view>
					</view>
					<view class="fengexian"></view>
					<!-- 联系人 -->
					<view class="flex-center">
						<view class="relive-title">
							<span class="font28">联系人</span>
						</view>
						<view class="relive-icon">
							<input placeholder-style="color:#CCCCCC" v-model="form.contact_name" placeholder="请填写联系人" />
							<image class="icon32" src="/static/image/icon_update.png" mode=""></image>
						</view>
					</view>
					<view class="fengexian"></view>
					<!-- 联系方式 -->
					<view class="flex-center">
						<view class="relive-title">
							<span class="font28">联系方式</span>
						</view>
						<view class="relive-icon">
							<input placeholder-style="color:#CCCCCC" v-model="form.contact_phone"
								placeholder="请填写联系方式" />
							<image class="icon32" src="/static/image/icon_update.png" mode=""></image>
						</view>
					</view>
				</view>
				<view class="box4 view-diy">
					<!-- 服务内容 -->
					<view class="flex-center">
						<view class="relive-title">
							<span class="font28">服务内容</span>
						</view>
						<view class="relive-icon">
							<input placeholder-style="color:#CCCCCC" v-model="form.service_content"
								placeholder="请填写服务内容" />
							<image class="icon32" src="/static/image/icon_update.png" mode=""></image>
						</view>
					</view>
					<view class="fengexian"></view>
					<!-- 服务流程 -->
					<view class="flex-center">
						<view class="relive-title">
							<span class="font28">服务流程</span>
						</view>
						<view class="relive-icon">
							<input placeholder-style="color:#CCCCCC" v-model="form.service_process"
								placeholder="请填写服务流程" />
							<image class="icon32" src="/static/image/icon_update.png" mode=""></image>
						</view>
					</view>
					<view class="fengexian"></view>
					<!-- 服务保障 -->
					<view class="flex-center">
						<view class="relive-title">
							<span class="font28">服务保障</span>
						</view>
						<view class="relive-icon">
							<input placeholder-style="color:#CCCCCC" v-model="form.service_guarantee"
								placeholder="请填写服务保障" />
							<image class="icon32" src="/static/image/icon_update.png" mode=""></image>
						</view>
					</view>
				</view>
				<view class="box5 view-diy">
					<!-- 营业资质 -->
					<view class="flex-center">
						<view class="relive-title">
							<span class="font28">营业资质</span>
						</view>
						<view class="relive-icon">
							<image class="icon22" src="/static/image/icon_gd.png" mode=""></image>
						</view>
					</view>
				</view>
			</view>
		</view>
		<!-- 设置 -->
		<view class="shop-setting">
			<span class="font24">设置</span>
			<view class="setting-relive flex-center view-diy">
				<view class="relive-title">
					<span class="font28">申请解除合作</span>
				</view>
				<view class="relive-icon">
					<image class="icon22" src="/static/image/icon_gd.png" mode=""></image>
				</view>
			</view>
		</view>


		<!-- 营业时间弹窗 -->
		<view class="pickerBox" v-if="hoursShow">
			<view class="black" @click="hoursShow=false"></view>

			<view class="pickerDate">
				<view class="handelBox">
					<view class="_cancel">
						<span class="font36" @click="hoursShow=false">取消</span>
					</view>
					<view class="_confirm">
						<span class="font36" style="color:cornflowerblue" @click="confirmEvent">确认</span>
					</view>
				</view>
				<view class="content">
					<picker-view :value="timeVal_start" @change="bindChange_start" class="picker-view"
						style="height:600rpx;flex: 1;">
						<picker-view-column>
							<view class="item" v-for="(item,index) in 23" :key="index">
								{{index}}时
							</view>
						</picker-view-column>
						<picker-view-column>
							<view class="item" v-for="(item,index) in 59" :key="index">
								{{index}}分
							</view>
						</picker-view-column>
					</picker-view>
					<view class="">
						<span>至</span>
					</view>
					<picker-view :value="timeVal_end" @change="bindChange_end" class="picker-view"
						style="height:600rpx;flex: 1;">
						<picker-view-column>
							<view class="item" v-for="(item,index) in 23" :key="index">
								{{index}}时
							</view>
						</picker-view-column>
						<picker-view-column>
							<view class="item" v-for="(item,index) in 59" :key="index">
								{{index}}分
							</view>
						</picker-view-column>
					</picker-view>

				</view>
			</view>

		</view>

		<!-- 保存 -->
		<view class="saveBox">
			<span @click="saveEvent">保存</span>
		</view>
	</view>
</template>

<script>
	export default {
		components: {},
		data() {
			return {
				// 我的店铺信息
				form: {
					"address": "", //商家地址
					"business_license": "", //营业执照
					"business_status": 0, //营业状态(0.休息中 1.营业中)
					"contact_name": "", //联系人
					"contact_phone": "", //	联系电话
					"createtime": "", //创建时间
					"house_number": "", //门牌号
					"id": 0, //编号
					"images": "", //相关图片
					"initial_delivery_fee": 0, //起送费
					"isvalid": 0, //是否有效(0有效 1无效)
					"latitude": "", //纬度
					"longitude": "", //	经度
					"operation_point_id": 0, //所属运营点ID
					"refuse_reason": "", //拒绝原因
					"remarks": "", //审核备注
					"score": 0, //评分
					"service_begin_time": "", //服务开始时间
					"service_content": "", //服务内容
					"service_end_time": "", //	服务结束时间
					"service_guarantee": "", //服务保障
					"service_process": "", //服务流程
					"shop_logo": "", //商家展示logo
					"shop_name": "", //商家名称
					"shop_sub_type_id": 0, //商家子类型ID
					"shop_type_id": 1, //商家类型ID
					"status": 0, //审核状态(-1申请入驻审核中 -2申请入驻被拒绝 0商家设置审核中 1审核通过 2商家设置被拒绝),1、商家入驻：等于null或-2时，可以申请，其余提示已入驻；2、商家管理：等于0时提示审核中，其余可以进入
					"user_id": 0 //用户ID
				},
				// 店铺类型
				shopType: [{
					"icon": "", //图标
					"id": 0, //编号
					"name": "", //商户类型名称
					"parentId": 0 //父级id
				}],
				typeIndex: 0,
				// 店铺状态
				shopStatus: [{
						label: '休息中',
						business_status: 0
					},
					{
						label: '营业中',
						business_status: 1
					}
				],
				statusIndex: 0,
				// 营业时间
				hoursShow: false,
				businessHours: ['开始时间', '结束时间'],
				timeVal_start: [9, 9],
				timeVal_end: [15, 15]
			}
		},
		created() {
			this.getTypeList()
		},
		methods: {
			// 获取商铺类型
			getTypeList() {
				this.util.ajax('shop/getShopTypeList', {
					"fromUserId": uni.getStorageSync('userId')
				}, res => {
					this.shopType = res.data.list

					this.getMyShop()
				})
			},
			// 获取我的店铺信息
			getMyShop() {
				this.util.ajax('shop/getShopInfo', {
					"userId": uni.getStorageSync('userId'),
				}, res => {
					this.form = res.data
				})
			},
			// 保存我的店铺信息
			saveEvent(){
				this.util.ajax('shop/saveShopSet', {
					"form": this.form,
				}, res => {
					console.log(res)
				})
			},

			// 监听商家类型变换
			shopTypeChange(e) {
				let that = this
				that.statusIndex = e.detail.value
			},

			// 监听营业状态变换
			shopStatusChange(e) {
				let that = this
				that.statusIndex = e.detail.value
				that.form.business_status = that.shopStatus[e.detail.value].business_status
			},

			//营业时间
			// 开始 结束时间监听
			bindChange_start(e) {
				let that = this
				that.timeVal_start = e.detail.value
			},
			bindChange_end(e) {
				let that = this
				that.timeVal_end = e.detail.value
			},
			// 确认
			confirmEvent() {
				let that = this;
				let start_h = '';
				let start_s = '';
				let end_h = '';
				let end_s = '';
				for (let i = 0; i < that.timeVal_start.length; i++) {
					if (that.timeVal_start[i] < 10) {
						if (i == 0) {
							start_h = '0' + that.timeVal_start[i]
						} else {
							start_s = '0' + that.timeVal_start[i]
						}
					} else {
						if (i == 0) {
							start_h = that.timeVal_start[i]
						} else {
							start_s = that.timeVal_start[i]
						}
					}
				}
				for (let i = 0; i < that.timeVal_end.length; i++) {
					if (that.timeVal_end[i] < 10) {
						if (i == 0) {
							end_h = '0' + that.timeVal_start[i]
						} else {
							end_s = '0' + that.timeVal_start[i]
						}
					} else {
						if (i == 0) {
							end_h = that.timeVal_end[i]
						} else {
							end_s = that.timeVal_end[i]
						}
					}
				}
				that.form.service_begin_time = start_h + ':' + start_s
				that.form.service_end_time = end_h + ':' + end_s

				this.hoursShow = false
			},
			//上传店铺头像
			updateImg() {
				uni.chooseImage({
					sourceType: ['album'], //从相册选择
					success: chooseImageRes => {
						console.log('成功', chooseImageRes);
						const tempFilePaths = chooseImageRes.tempFilePaths;
						this.form.images = tempFilePaths[0];
						// uni.uploadFile({
						// 	url: '', //仅为示例，非真实的接口地址
						// 	filePath: tempFilePaths[0],
						// 	name: 'file',
						// 	header: {
						// 		accessToken: uni.getStorageSync('accessToken'),
						// 		platform: 2,
						// 		type: 1
						// 	},
						// 	formData: {
						// 		category: 3
						// 	},
						// 	success: res => {
						// 		// console.log('上传成功', JSON.parse(res.data));
						// 		// uploadFile上传成功后，根据和后台的约定msgCode判断接口调用状态
						// 		let data = JSON.parse(res.data);
						// 		// 成功：获取到头像
						// 		if (data.msgCode == 200) {

						// 		}
						// 		// 失败报错
						// 		if (data.msgCode == 500) {
						// 			this.$alert(data.msgContent, 'none');
						// 		}
						// 		// 登陆过期
						// 		if (data.msgCode == 311 || data.msgCode == 312) {

						// 		}
						// 	}
						// });
					},
					fail: err => {
						this.$alert('图片上传失败', 'none');
					}
				});
			},
		}
	}
</script>

<style lang="less">
	.flex-center {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.fengexian {
		height: 1px;
		background: #E8E8E8;
		margin: 20rpx 0;
	}

	.shop-setting {
		padding-bottom: 100rpx;
	}

	.relive-icon {
		display: flex;
		text-align: right;
		align-items: center;
		position: relative;

		.timeSlot,
		.uni-datetime-picker {
			position: absolute;
		}

		.icon32 {
			margin-left: 5rpx;
		}

		input {
			width: 400rpx;
			color: #666666;
			font-size: 26rpx;
			text-align: right;
		}
	}

	.shop {
		padding: 25rpx;

		.setting-relive,
		.news-box {
			margin-top: 20rpx;
		}
	}

	.status {
		span {
			font-size: 26rpx;
		}

		.status-switch {
			padding: 8rpx 19rpx;
			border-radius: 25rpx;
			background-color: #F6F6F6;
			display: flex;
			align-items: center;
			text-align: center;

			.switch {
				padding: 8rpx;
				border-radius: 8rpx;
			}

			.shut {
				background-color: red;
			}

			.open {
				background-color: #6ED500;
			}

			.text {
				margin-left: 15rpx;
			}
		}
	}

	.pickerBox {
		.black {
			position: fixed;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background-color: rgba(0, 0, 0, 0.4);
		}

		.pickerDate {
			width: 100%;
			background-color: #fff;
			position: fixed;
			left: 0;
			bottom: 0;

			.handelBox {
				display: flex;
				justify-content: space-between;
				padding: 20rpx 30rpx;
			}

			.content {
				display: flex;
				align-items: center;
				padding-bottom: 0;

				.item {
					display: flex;
					align-items: center;
					justify-content: center;
					text-align: center;
				}
			}
		}
	}

	.saveBox {
		width: 100%;
		padding: 30rpx 0;
		background-color: #fff;
		text-align: center;
		position: fixed;
		left: 0;
		bottom: 0;
		box-shadow: 0px 6rpx 30rpx #888888;

		span {
			font-size: 36rpx;
			padding: 10rpx 50rpx;
			background: linear-gradient(180deg, #FDEC7E 0%, #F9D448 100%);
			border-radius: 7px;
		}
	}
</style>
